import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";


const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name:'first',
    component: () => import("@/views/FirstPage.vue")
  },
  {
    path: "/main",
    name: "main",
    redirect:'/home',
    component: () => import("@/views/MainPage.vue"),
    children:[

      {path:'/home',
      name:'home',
      component:() => import("@/views/HomeView.vue")
      },

      {
        path:'/articles',
        name:'articles',
        component:() => import('@/views/ArticleView.vue')
      },

      {
        path:'/time',
        name:'time',
        component:() => import('@/views/TimeLine.vue')
      }
    ]
  },

  {
    path:'/articlemain',
    name:'articlemain',
    component: () => import('@/views/MainArticle.vue'),
    children:[
      {
        path:'/css1',
        name:'css1',
        component:() => import('@/views/articles/CssOne.vue')
      },
      {
        path:'/js1',
        name:'js1',
        component:() => import('@/views/articles/JsOne.vue')
      },
      {
        path:'/vue1',
        name:'vue1',
        component:() => import('@/views/articles/VueOne.vue')
      },
      {
        path:'/other1',
        name:'other1',
        component: () => import('@/views/articles/OtherOne.vue')
      },
      {
        path:'/vue2',
        name:'vue2',
        component: () => import('@/views/articles/VueTwo.vue')
      },
      {
        path:'/js2',
        name:'js2',
        component: () => import('@/views/articles/JsTwo.vue')
      }

    ]
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
